import React, { useState, useEffect } from "react";
import { Form, Button, Container, Row, Col } from "react-bootstrap";
import { useLocation, Link } from "react-router-dom";
import { IoMdAdd } from "react-icons/io";
import Moment from "moment";

import logoNoir from "../../img/logoNoir.svg";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { registerLocale } from "react-datepicker";
import fr from "date-fns/locale/fr-CA";
registerLocale("fr", fr);
/*Fonction pour l'dition d'une demande de stage*/
function FormEditDemande(props) {
  //Dclare une variable pour le local storage
  var ls = require("local-storage");
  //Variable pour connatre la page o je me trouve, pour aller chercher des informations dans l'url
  let location = useLocation();
  //Variable pour rcuprer l'id dans l'url, avec la proprit search
  var stringId = location.search.replace("?id=", "");
  //Variables pour les informations du formulaire
  let [secteurs, setSecteurs] = useState([]);
  let [secteurChoisi, setSecteurChoisi] = useState("");
  let [dateDebutEdit, setDateDebutEdit] = useState(new Date());
  let [dateFinEdit, setDateFinEdit] = useState(new Date());
  let [change, setChange] = useState(false);

  let [autresFormationsEdit, setAutresFormationsEdit] = useState([]);
  let [competences, setCompetences] = useState([]);
  //Variable des informations de la demande slectionne
  const [objetDemande, setObjetDemande] = useState({
    titre: "",
    secteurActivite: "",
    ville: "",
    dateDebut: new Date(),
    dateFin: new Date(),
    duree: null,
    description: "",
    nbHeuresSemaine: null,
    autresInformations: "",
    programmeSuivi: "",
    autresFormations: [],
    competences: [],
    typestage: "",
    remunere: "",
    dateParution: new Date(),
    etudiant: "",
    actif: null,
    valide: null,
    supprime: null,
    vedette: null,
  });
  //useEffect, Obtenir les demandes de stage et la liste des secteurs d'activit
  useEffect(() => {
    getDemande();
  }, []);
  useEffect(() => {
    getSecteurs();
  }, []);
  useEffect(() => {
    setAutresFormationsEdit(objetDemande.autresFormations);
    setCompetences(objetDemande.competences);
    setChange(false);
  }, [objetDemande]);

  //On rcupre les infos de la bd de la demande slectionne
  async function getDemande() {
    //Variable pour les donnes de la demande slectionne
    try {
      const response = await fetch(
        process.env.REACT_APP_API +
          process.env.REACT_APP_DEMANDES +
          "/" +
          stringId
      );
      const reponseDeApi = await response.json();
      setObjetDemande(reponseDeApi);

      setDateDebutEdit(Moment(reponseDeApi.dateDebut).add(1, "days"));
      setDateFinEdit(Moment(reponseDeApi.dateFin).add(1, "days"));
      if (!response.ok) {
        //Permet d'attraper l'erreur 500 et l'erreur 400
        throw Error(response.statusText);
      }
    } catch (error) {
      //On gre l'erreur
      console.log(error);
    }
  }

  //On rcupre les infos de la bd de la liste des secteurs d'activit
  async function getSecteurs() {
    //Variables pour les donnes de l'tudiant qui fait la demande
    try {
      const response = await fetch(
        process.env.REACT_APP_API + process.env.REACT_APP_SECTEURS
      );
      const reponseDeApi = await response.json();
      setSecteurs(reponseDeApi);
      if (!response.ok) {
        //Permet d'attraper l'erreur 500 et l'erreur 400
        throw Error(response.statusText);
      }
    } catch (error) {
      //On gre l'erreur
      console.log(error);
    }
  }

  //Mthode pour modifier la bd avec le formulaire, semblable  l'ajout ajouter l'id  l'URL et changer la methode pour PUT
  async function editDemande(
    titre,
    ville,
    duree,
    description,
    nbHeuresSemaine,
    autresInfos,
    programmeSuivi,
    type,
    remuneration
  ) {
    try {
      const response = await fetch(
        process.env.REACT_APP_API +
          process.env.REACT_APP_DEMANDES +
          "/" +
          stringId,
        {
          /*Pour un ajout utiliser la mthode POST */
          method: "PUT",
          /*Pour un ajout ajouter un headers */
          headers: {
            "Content-Type": "application/json",
          },

          body: JSON.stringify({
            titre: titre,
            secteurActivite: secteurChoisi,
            ville: ville,
            dateDebut: Moment(dateDebutEdit).add(-1, "days"),
            dateFin: Moment(dateFinEdit).add(-1, "days"),
            duree: duree,
            description: description,
            nbHeuresSemaine: nbHeuresSemaine,
            autresInformations: autresInfos,
            programmeSuivi: programmeSuivi,
            autresFormations: autresFormationsEdit,
            competences: competences,
            typestage: type,
            remunere: remuneration,
            dateParution: objetDemande.dateParution,
            etudiant: objetDemande.etudiant,
            //Pas ncessaire de modifier les paramtres (actif, valide et supprime), cette tape se fait lors de la validation ou le bouton supprimer
            actif: true,
            valide: true,
            supprime: false,
            vedette: objetDemande.vedette,
          }),
        }
      );
      if (response.ok) {
        //const jsonResponse = await response.json();
        props.history.push(
          "/admin/" + ls.get("nom") + "?niveau=" + ls.get("niveau")
        ); //Retour  la page d'accueil

        return response;
      }
      throw new Error("Request failed!");
    } catch (error) {
      console.log(error);
    }
  }
  /* Va chercher les infos des inputs du formulaire */
  function handleEdit(event) {
    event.preventDefault(); /*Empche de rafrachir la page, car le bouton est de type submit*/

    /*variables des infos entres dans le formulaire*/
    const titreEdit = document.getElementById("titreId").value;
    const villeEdit = document.getElementById("villeId").value;
    const dureeEdit = document.getElementById("dureeId").value;
    const descriptionEdit = document.getElementById("descriptionId").value;
    const heuresSemaineEdit = document.getElementById("heuresSemaineId").value;
    const autresInfosEdit = document.getElementById("autresInfosId").value;
    const programmeSuiviEdit = document.getElementById("programmeSuiviId")
      .value;
    const typeStageEdit = document.getElementById("typeStageId").value;
    const remunerationEdit = document.getElementById("remunereId").value;
    /*Fonction pour entrer les infos dans la bd */
    /*Ajouter les comptences et autres Formations provenant d'une variable tableau */
    editDemande(
      titreEdit,
      villeEdit,
      dureeEdit,
      descriptionEdit,
      heuresSemaineEdit,
      autresInfosEdit,
      programmeSuiviEdit,
      typeStageEdit,
      remunerationEdit
    );
  }

  //Fonction pour le changement du secteur d'activit
  function handleChangeSecteur(e) {
    setSecteurChoisi(e.target.value);
  }

  //Fonctions lors d'un changement dans les autres formations
  //Si la valeur du checked est true ajouter la valeur au tableau, sinon effacer  partir de l'index
  function handleChangeFormations(i) {
    let tabFormations = autresFormationsEdit;
    tabFormations.splice(i, 1);
    setAutresFormationsEdit(tabFormations);
    change ? setChange(false) : setChange(true);
  }
  //Fonctions lors d'un changement dans les autres formations
  //Si la valeur du checked est true ajouter la valeur au tableau, sinon effacer  partir de l'index

  function handleChangeCompetences(i) {
    let tabCompetences = competences;
    tabCompetences.splice(i, 1);
    setCompetences(tabCompetences);
    change ? setChange(false) : setChange(true);
  }

  //Fonction pour ajouter une formation supplmentaire
  function handleClickAjoutFormation() {
    let tabformations = autresFormationsEdit;
    let formationAjoutee = document.getElementById("formationId").value;
    tabformations.push(formationAjoutee);
    setAutresFormationsEdit(tabformations);
    change ? setChange(false) : setChange(true);
  }
  //Fonction pour ajouter une comptence supplmentaire
  function handleClickAjoutCompetence() {
    let tabcompetences = competences;
    let competenceAjoutee = document.getElementById("competenceId").value;
    tabcompetences.push(competenceAjoutee);
    setCompetences(tabcompetences);
    change ? setChange(false) : setChange(true);
  }

  return (
    <Container fluid id="formEdit">
      <Row>
        <Link to="/">
          <img src={logoNoir} alt="logo" className="w-50 mb-5 logo" />
        </Link>
      </Row>
      <Container>
        <Row>
          <Col xs={12}>
            <h1 className="my-5 text-center font-large">
              Modifier la demande {objetDemande._id}
            </h1>
          </Col>
        </Row>
        <Row className="fondGris">
          <Col xs={12}>
            <Form className="py-5">
              <Form.Group controlId="titreId">
                <Form.Label>Titre de la demande</Form.Label>
                <Form.Control type="text" defaultValue={objetDemande.titre} />
              </Form.Group>
              <Form.Group>
                <select id="selectSecteur" onChange={handleChangeSecteur}>
                  {secteurs.map((item) => (
                    <option
                      selected={item._id === objetDemande.secteurActivite}
                      key={"key" + item._id}
                      value={item.nom}
                    >
                      {item.nom}
                    </option>
                  ))}
                </select>
              </Form.Group>
              <Form.Group controlId="villeId">
                <Form.Label>Ville</Form.Label>
                <Form.Control type="text" defaultValue={objetDemande.ville} />
              </Form.Group>

              {/*****Il faudra ajouter la valeur par dfaut des
                datepickers*******/}

              <label className="d-block">Date du dbut du stage</label>

              <DatePicker
                utcOffset
                locale="fr"
                dateFormat="MM/dd/yyyy"
                onChange={(evt) => setDateDebutEdit(Moment(evt))}
                selected={Moment(dateDebutEdit).toDate()}
              />
              <label className="d-block">Date de fin du stage</label>

              <DatePicker
                utcOffset
                locale="fr"
                dateFormat="MM/dd/yyyy"
                onChange={(evt) => setDateFinEdit(Moment(evt))}
                selected={Moment(dateFinEdit).toDate()}
              />
              <Form.Group>
                <Form.Label className="mr-2">
                  Dure en nombre de semaines
                </Form.Label>
                <input
                  id="dureeId"
                  type="number"
                  defaultValue={objetDemande.duree}
                />
              </Form.Group>
              <Form.Group controlId="descriptionId">
                <Form.Label>Description</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={8}
                  defaultValue={objetDemande.description}
                />
              </Form.Group>
              <Form.Group>
                <Form.Label className="mr-2">
                  Nombre d'heures/semaine
                </Form.Label>
                <input
                  id="heuresSemaineId"
                  type="number"
                  defaultValue={objetDemande.nbHeuresSemaine}
                />
              </Form.Group>
              {/*Changer pour un textarea */}
              <Form.Group controlId="autresInfosId">
                <Form.Label>Informations supplmentaires</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={8}
                  defaultValue={objetDemande.autresInformations}
                />
              </Form.Group>
              <Form.Group controlId="programmeSuiviId">
                <Form.Label>Programme suivi</Form.Label>
                <Form.Control
                  type="text"
                  defaultValue={objetDemande.programmeSuivi}
                />
              </Form.Group>
              {/*Les autres formations et les comptences sont affiches avec des checkbox*/}
              <h3>Formations supplmentaires</h3>
              {autresFormationsEdit.map((item, i) => {
                return (
                  <div>
                    <input
                      id={item}
                      type="checkbox"
                      value={item}
                      checked
                      onClick={() => handleChangeFormations(i)}
                    />
                    <label for={item} className="ml-2">
                      {item}
                    </label>
                  </div>
                );
              })}
              <input type="text" className="mr-2" id="formationId"></input>
              <Button type="button" onClick={handleClickAjoutFormation}>
                <IoMdAdd onClick={() => handleClickAjoutFormation} />
              </Button>

              <h3>Comptences</h3>
              <Form.Group controlId="checkboxCompetences">
                {competences.map((item, i) => {
                  return (
                    <div>
                      <input
                        id={item}
                        type="checkbox"
                        value={item}
                        checked
                        onChange={() => handleChangeCompetences(i)}
                      />
                      <label for={item} className="ml-2">
                        {item}
                      </label>
                    </div>
                  );
                })}
                <input type="text" className="mr-2" id="competenceId"></input>
                <Button type="button" onClick={handleClickAjoutCompetence}>
                  <IoMdAdd onClick={() => handleClickAjoutCompetence} />
                </Button>
              </Form.Group>
              {/*Ajouter un label,  Voir pour changer pour des boutons radio*/}
              <Form.Group>
                <h6>Type de stage</h6>
                <select id="typeStageId">
                  {/*pour garder la valeur dans le formulaire */}
                  <option selected>{objetDemande.typestage}</option>
                  <option value="Temps plein">Temps plein</option>
                  <option value="Temps partiel">Temps partiel</option>
                  <option value="Alternance travail tude">
                    Alternance travail tude
                  </option>
                </select>
              </Form.Group>
              {/*Ajouter un label,  Voir pour changer pour des boutons radio*/}
              <Form.Group>
                <h6>Rmunration</h6>
                <select id="remunereId">
                  {/*pour garder la valeur dans le formulaire */}
                  <option selected>{objetDemande.remunere}</option>
                  <option value="OUI">OUI</option>
                  <option value="NON">NON</option>
                  <option value=" la discrtion de l'entreprise">
                     la discrtion de l'entreprise
                  </option>
                </select>
              </Form.Group>
              {/*ID de l'tudiant */}
              {/* Pour l'administrateur l'id est entr, pour l'tudiant la variable local ou le props */}
              <Form.Group controlId="etudiantId">
                <Form.Label>ID de l'tudiant</Form.Label>
                <Form.Control
                  disabled
                  type="text"
                  defaultValue={objetDemande.etudiant}
                />
              </Form.Group>
              {/*Changer pour un checkbox */}
              <Form.Group controlId="vedetteId">
                <Form.Label>Demande vedette</Form.Label>
                <Form.Control
                  disabled
                  type="text"
                  defaultValue={objetDemande.vedette}
                />
              </Form.Group>
            </Form>
            <Button
              id="enregistrer"
              className="my-5"
              variant="primary"
              type="submit"
              onClick={handleEdit}
            >
              Enregistrer
            </Button>
            <Link
              className="btn btn-primary"
              to={
                "/admin/" +
                ls.get("nom") +
                "?niveau=" +
                ls.get("niveau").toString()
              }
            >
              Annuler
            </Link>
          </Col>
        </Row>
      </Container>
    </Container>
  );
}

export default FormEditDemande;
